@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap');
* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  font-family: 'Source Code Pro', monospace;
}
body {
  background-color: antiquewhite;
  font-family: "Style Script", cursive;
}
.Title {
  font-size: 30px;
  font-weight: bold;
  text-align: center;
  text-transform: capitalize;
  width: 35%;
  margin: 10% auto 0% auto;
  border: none;
  border-bottom: 1px solid black;
}
.mainBox {
  background: white;
  width: 40%;
  text-align: center;
  text-transform: capitalize;
  font-size: 30px;
  margin: 2% auto;
  border-radius: 20px;
  box-shadow: 0px 0px 3px 5px lightgrey;
  font-weight: bold;
  padding: 10px;
}

@media (min-width: 250px) and (max-width: 500px) {
  body {
    background: gray;
  }
  .Title {
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    text-transform: capitalize;
    width: 70%;
    margin: 10% auto 0% auto;
    border: none;
    border-bottom: 1px solid black;
  }
  .mainBox {
    background: white;
    width: 70%;
    text-align: center;
    text-transform: capitalize;
    font-size: 25px;
    margin: 10% auto;
    border-radius: 20px;
    box-shadow: lightgrey 0px 0px 3px 5px;
    font-weight: bold;
    padding: 10px;
  }
}
